.container{
  display: flex;
  align-items: center;
  flex-direction: column;
}

.container-item{
  display: flex;
  /* align-items: flex-start; */
  gap: 10px;
  margin-top: 10px;
  flex-wrap: wrap;
  margin-bottom: 30px;
}
.wrap{
  width: 60%;
}
.container-item li{
  width: 100%;
}


.input{
  border-right: none;
  border-top: none;
  border-left: none;
  padding: 6px 12px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border-bottom: 1px solid #ced4da;
  appearance: none;
  border-radius: 4px;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.input:focus-visible{
  outline: none;
}
 

header{
  display: flex;
  justify-content: space-around;
  /* margin: 10px; */
  height: 70px;
  background: yellow;
  align-items: center;

  
}

textarea, input {
  display: block;
  box-sizing: padding-box;
  overflow: hidden;
  padding: 10px;
  width: 100%;
  font-size: 14px;
  margin: 30px auto;
  border-radius: 6px;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.3);
  border: 0;
}
textarea:focus,input:focus {
  border: none;
  outline: none;
}

* {
  box-sizing: border-box;
}

.todo-item{
  width: 32%;
  border-radius: 5px;
    border: 1px solid #ccc;
  height: auto;
  /* box-shadow: 2px 2px 2px 1px rgb(0 0 0 / 20%); */
  padding: 10px;
  margin: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  word-break: break-word;
  animation: fromTop 0.5s linear;
}

@keyframes fromTop {
  0% {
    transform: translateY(-20%);
  }
  100% {
    transform: translateY(0%);
  }
}
.todo-item.active{
  background-color: #faee3870;
  box-shadow:1px 3px 11px 1px rgb(0 0 0 / 20%)
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: yellow;
  color: #000;
  text-align: center;
}

.todo-item-delete{
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  cursor:pointer
}

.todo-item-empty{
  display: flex;
  justify-content: center;
  width: 100%;

}
.todo-item:hover{
  box-shadow:1px 3px 11px 1px rgb(0 0 0 / 20%)
}

@media only screen and (max-width: 600px) {
  .todo-item{
    width: 100%;
  }
}


@media only screen and (max-width: 1024px) {
  .todo-item{
    width: 31%;
  }
}